import React from "react"
import "./Confetti.scss"
export interface ConfettiProps {
  count: number
}
export default class Confetti extends React.Component<ConfettiProps> {
  ref: React.RefObject<HTMLDivElement>

  constructor(props: ConfettiProps) {
    super(props)
    this.ref = React.createRef()
  }
  render() {
    return (
      <div
        className = {"mainConfetti"}
        hidden = {true}
        ref = {this.ref}
      >
        {
          Array(this.props.count)
            .fill(1)
            .map((el, i) => (
              <span
                className = {`confetti-${i}`}
                key = {i}
              />
            ))
        } 
      </div>
    )
  }

  show() {
    this.ref.current.hidden = false
  }
  hide() {
    this.ref.current.hidden = true
  }
}