import React from "react"
import PointerController from "./PointerController"
import CrossIcon from "-!svg-react-loader!../../assets/svg/close.svg"
import removeClassStartWith from "../../utils/removeClassStartWith"
export interface SquareProps {
  initValue: number,
  solvedValue: number, // 0 is empty, 1 is active, -1 is inactive
  color: number,
  indexX: number,
  indexY: number,
  pointerController: PointerController
}

export interface SquareState {
  value: number
}

export default class Square extends React.Component<SquareProps, SquareState> {
  ref: React.RefObject<HTMLDivElement>
  square: React.RefObject<HTMLDivElement>
  color: React.RefObject<HTMLSpanElement>
  activeRef: React.RefObject<HTMLSpanElement>

  initValue: number
  solvedValue: number // 0 is empty, 1 is active, -1 is inactive
  x: number
  y: number
  pointerController: PointerController

  crossIcon: React.RefObject<HTMLDivElement>

  onAnimationEnd: Function

  constructor(props: SquareProps) {
    super(props)
    this.state = {
      value: 0
    }
    this.ref = React.createRef()
    this.square = React.createRef()
    this.color = React.createRef()
    this.activeRef = React.createRef()
    this.crossIcon = React.createRef()

    this.initValue = this.props.initValue
    this.solvedValue = this.props.solvedValue
    this.x = this.props.indexX
    this.y = this.props.indexY
    this.pointerController = this.props.pointerController
  }

  shouldComponentUpdate(nextProps: Readonly<SquareProps>, nextState: Readonly<SquareState>) {
    return false
  }

  public render(): JSX.Element {
    console.log("squareRender")
    const isActive = this.state.value == 1
    const value = this.state.value
    let className = `square`
    if (isActive) className += ' square-active'
    if (this.props.indexX == 0 && this.props.indexY == 0) {
      
    } else if (this.props.indexX == 0) {
      className += ' square-border-l'
    } else if (this.props.indexY == 0) {
      className += ' square-border-t'
    } else {
      className += ' square-border-l square-border-t'
    }
    return (
      <div
        className = "grid-item"
        ref = {this.ref}
        onAnimationEnd = {((e:AnimationEvent) => {this.onAnimationEnd(e); e.stopPropagation()}).bind(this)}
      >
        <div 
          ref = {this.square}
          className={className} 
        >
          <div
            className = "square-div-icon"
            ref = {this.crossIcon}
            hidden = {value != -1}
          >
            <CrossIcon 
              className = {"cross-icon"}
            />
          </div>
        </div>
        <span
          className = {'square-active-color'}
          hidden = {!isActive}
          ref = {this.activeRef}
        />
        <span
          ref = {this.color}
        ></span>
      </div>
    )
  }

  process() {
    if (this.state.value != 0) return 0
    const solvedValue = this.props.solvedValue
    this.setState({value: solvedValue})
    if (solvedValue === 1) {
      this.square.current.className += ' square-active'
      this.activeRef.current.hidden = false
    }
    else if (solvedValue === -1) {
      this.playerAnimationError()
    }
    return solvedValue
  }

  playAnimationComplete(delay = 0, duration = 500) {
    setTimeout( () => {
      this.color.current.style.setProperty('--animation-duration', `${duration}ms`)
      
      this.color.current.className += ' animate-complete'

      this.onAnimationEnd = (e: AnimationEvent) => {
        this.color.current.className = removeClassStartWith(this.color.current.className, 'animate')
        e.stopPropagation()
      }
    }, delay)
    
  }

  playerAnimationError(delay = 0, duration = 1000) {
    setTimeout( () => {
      this.color.current.style.setProperty('--animation-duration', `${duration}ms`)
  
      this.color.current.className += ' animate-error'
  
      this.onAnimationEnd = (e: AnimationEvent) => {
        this.color.current.className = removeClassStartWith(this.color.current.className, 'animate')  
        e.stopPropagation()
      }
      setTimeout( () => {
        this.playFadeInCross()
      }, duration)
    }, delay)
    
  }

  playFadeInCross(delay = 0, duration = 200) {
    setTimeout( ( () => {
      this.crossIcon.current.style.setProperty('--animation-duration', `${duration}ms`)
      this.crossIcon.current.hidden = false
      this.crossIcon.current.className += ' animate-fade-in'
      this.onAnimationEnd = ((e: AnimationEvent) => {
        this.crossIcon.current.className = removeClassStartWith(this.crossIcon.current.className, 'animate')
        e.stopPropagation()
      }).bind(this)
    }).bind(this), delay)
    
  }

  playAnimationBoardComplete(delay = 0, duration = 500) {
    setTimeout( (()=>{
      this.color.current.style.setProperty('--animation-duration', `${duration}ms`)
      this.color.current.style.setProperty('--animation-square-board-complete', `#${this.props.color.toString(16)}`)
  
      this.color.current.className += ' animate-square-board-complete'
      
      this.onAnimationEnd = ((e: AnimationEvent) => {
        // this.ref.current.className = removeClassStartWith(this.ref.current.className, 'animate')
        e.stopPropagation()
      }).bind(this)
    }).bind(this), delay)
    
  }
}
