import GridBoard from "./GridBoard"
import { IPosition } from "../../common/Interfaces/IPosition"
import { HORIZONTAL, NON_DIR, VERITCAL } from "../../constants/gameConstants"

export default class PointerController {
  gridBoard: GridBoard
  isPointerDown = false
  currentSquareIndex: IPosition
  direction: string
  mode = 1
  constructor(gridBoard: GridBoard) {
    this.gridBoard = gridBoard
    window.document.addEventListener('pointerup', this.onPointerUp.bind(this))
  }

  handlePointerDown(pos: IPosition, e: PointerEvent) {
    if (!this.isPointerDown) {
      return this.processAtPointerDown(pos, e)
    }
    return 0
  }

  handlePointerMove(pos: IPosition, e: PointerEvent) {
    if (this.isPointerDown) {
      if (this.direction === NON_DIR) {
        return this.processAtSecondSquare(pos, e)
      } else {
        return this.processAtPointerMove(pos, e)
      }
    }
    return 0
  }

  processAtPointerDown(pos: IPosition, e: PointerEvent) {
    const {x, y} = pos
    this.isPointerDown = true
    this.currentSquareIndex = this.gridBoard.getSquareIndexAtPosition(x, y)
    this.direction = NON_DIR
    this.processAt(this.currentSquareIndex.x, this.currentSquareIndex.y, e)
    return true
  }

  processAtSecondSquare(pos: IPosition, e: PointerEvent) {
    const {x, y} = pos
    if (!this.isPointerDown) return 0
    const currentSquareIndex = this.gridBoard.getSquareIndexAtPosition(x, y)
    
    if (currentSquareIndex.x === this.currentSquareIndex.x && currentSquareIndex.y != this.currentSquareIndex.y) 
    {
      this.direction = HORIZONTAL
      return this.processToY(currentSquareIndex.y, e)
    } 
    else if (currentSquareIndex.y === this.currentSquareIndex.y && currentSquareIndex.x != this.currentSquareIndex.x)
    {
      this.direction = VERITCAL
      return this.processToX(currentSquareIndex.x, e)
    }
    return false
  }

  processAtPointerMove(pos: IPosition, e: PointerEvent) {
    const {x, y} = pos
    if (!this.isPointerDown) return 0
    const currentSquareIndex = this.gridBoard.getSquareIndexAtPosition(x, y)
    if (this.direction === HORIZONTAL) {
      return this.processToY(currentSquareIndex.y, e)
    } else {
      return this.processToX(currentSquareIndex.x, e)
    }
  }

  processToX(x: number, e: PointerEvent) {
    const min = Math.min(x, this.currentSquareIndex.x)
    const max = Math.max(x, this.currentSquareIndex.x)
    let processValue = 0
    for (let i = min; i <= max; i++) {
      processValue = this.processAt(i, this.currentSquareIndex.y, e)
    }
    this.currentSquareIndex.x = x
    return processValue
  }

  processToY(y: number, e: PointerEvent) {
    const min = Math.min(y, this.currentSquareIndex.y)
    const max = Math.max(y, this.currentSquareIndex.y)
    let processValue = 0
    for (let i = min; i <= max; i++) {
      processValue = this.processAt(this.currentSquareIndex.x, i, e)
    }
    this.currentSquareIndex.y = y
    return processValue
  }

  processAt(indexX: number, indexY: number, e: PointerEvent) {
    if (!this.isPointerDown) return 0
    const processValue = this.gridBoard.processAt(indexX, indexY)
    if (processValue === 0) return processValue
    if (processValue != this.mode) {
      this.reset()
      e.stopPropagation()
    }
    return processValue
  }

  onPointerUp() {
    this.reset()
  }

  reset() {
    this.isPointerDown = false 
    this.direction = NON_DIR
  }
}