import React from "react"
import "./Light.scss"

export interface BackgroundLightProps {
  count: number
}
export default class BackgroundLight extends React.Component<BackgroundLightProps> {
  ref: React.RefObject<HTMLDivElement>
  constructor (props: BackgroundLightProps) {
    super(props)
    this.ref = React.createRef()
  }
  render() {
    const {count} = this.props 
    return (
      <div
        className = "light-background"
        ref = {this.ref}
      >
        {
          Array(count)
            .fill(count)
            .map((el, i) =>
              <div
                className = "ray"
                key = {i}
                style = {{["--angle" as any]: `${360/count * i}deg`}}
              >
              </div>
            )
        }
      </div>
    )
  }

  show(delay = 0, duration = 500) {
    setTimeout( () => {
      this.ref.current.style.setProperty('--animation-duration', `${duration}ms`)
      this.ref.current.hidden = false
      this.ref.current.className += ' animate-fade-in'
    }, delay)
  }
}