import React from "react"

export interface PixArtProps {
  size: number
  color?: number[][]
}

export default class PixArt extends React.Component<PixArtProps, any> {
  ref: React.RefObject<HTMLDivElement>
  pixels: React.RefObject<HTMLDivElement>[][]
  constructor(props: PixArtProps) {
    super(props)
    this.pixels = []
    for (let i = 0 ; i < this.props.size ; i++) {
      const row = []
      for (let j = 0 ; j < this.props.size ; j ++) {
        row.push(React.createRef<HTMLDivElement>())
      }
      this.pixels.push(row)
    }
    this.ref = React.createRef<HTMLDivElement>()
  }
  render(): JSX.Element {
    const { size, color } = this.props
    return (
      <div
        className = {`pixart-${size}`}
        ref = {this.ref}
      >
        {
          Array(size)
            .fill(1)
            .map((el, x) => Array(size)
              .fill(1)
              .map(
                (el, y) => (
                  <div
                    className = "pixel"
                    ref = {this.pixels[x][y]}
                    key = {`${x}-${y}`}
                    property = {`--pixel-color:#${color? color[x][y].toString(16): 'ffffff'}`}
                  > 
                  </div>
                )
              )
            )
        }        
      </div>
    )
  }

  setColor(x: number, y: number, color: number) {
    this.pixels[x][y].current.style.setProperty('--pixel-color', `#${color.toString(16)}`)
  }

  playAnimationBoardComplete(delay = 0, duration = 500) {
    setTimeout( (() => {
      this.ref.current.style.setProperty('--animation-duration', `${duration}ms`)

      this.ref.current.className += ' animate-fade-out'
  
      this.ref.current.onanimationend = (e: AnimationEvent) => {
        
        e.stopPropagation()
      }
    }).bind(this),
    delay)
  }
}