import React from "react"
import { VERITCAL } from "../../constants/gameConstants"
import Tag from "./Tag"

export interface ListTagsProps {
  direction: string
  size: number
  listNumber: number[][]
}

export interface ListTagsState {
  isActive?: boolean
}

export default class ListTags extends React.Component<ListTagsProps, ListTagsState> {
  tags: React.RefObject<Tag>[]
  constructor(props: ListTagsProps) {
    super(props)

    this.tags = []
    for (let i = 0 ; i < this.props.size; i++) {
      const ref = React.createRef<Tag>()
      this.tags.push(ref)
    }
  }

  render(): JSX.Element {
    const {direction, size, listNumber} = this.props
    let className = 'list-tag-'
    if (direction === VERITCAL) {
      className += 'v'
    } else {
      className += 'h'
    }

    className += `-${size}`
    return (
      <div 
        className = {className}
      >
        {Array(size)
          .fill(1)
          .map(
            (el, i) => (
              <Tag 
                key = {i}
                direction = {direction}
                listNumber = {listNumber[i]}
                ref = {this.tags[i]}
              />
            )
          )
        }
      </div>
    )
  }
}