import React from "react"
import { HORIZONTAL } from "../../constants/gameConstants"
import ListTags from "./ListTags"
import GridBoard from "./GridBoard"
import { VERITCAL } from './../../constants/gameConstants'
import "./Board.scss"
import PixArt from "./PixArt"
import Tag from './Tag'
import Square from './Square'
import removeClassStartWith from "../../utils/removeClassStartWith"
import distance from "../../utils/distance"
import GamePage from "../Page/GamePage"

const DELAY = 25

export interface BoardProps {
  initBoard: number[][]
  solvedBoard: number[][]
  coloredBoard: number[][]
  size: number
  gamePage: GamePage
}
export default class Board extends React.Component<BoardProps> {
  row: number [][]
  column: number[][]
  
  ref: React.RefObject<HTMLDivElement>

  pixArtRef: React.RefObject<PixArt>
  pixArt: PixArt

  gridBoardRef: React.RefObject<GridBoard>
  gridBoard: GridBoard
  squares: Square[][]

  listTagColumnRef: React.RefObject<ListTags>
  tagsColumn: Tag[] 

  tagsRowRef: React.RefObject<ListTags>
  tagsRow: Tag[] 

  currentBoard: number [][]

  countSolved: number
  numberOfActiveValueSquare: number
  isComplete: boolean 

  onAnimationEnd: Function = () => {}
  constructor(props: BoardProps) {
    super(props)
    const { initBoard, solvedBoard, size} = this.props
    const {row, column} = this.getTagValue(solvedBoard)
    this.countSolved = 0
    this.numberOfActiveValueSquare = solvedBoard.reduce( (p, v) => p.concat(v)).filter(v => v==1).length
    this.isComplete = false
    this.currentBoard = initBoard.map(row => row.map(value => value))
    
    this.row = row 
    this.column = column

    this.ref = React.createRef<HTMLDivElement>()

    this.pixArtRef = React.createRef<PixArt>()

    this.gridBoardRef = React.createRef<GridBoard>()

    this.listTagColumnRef = React.createRef<ListTags>()
    this.tagsRowRef = React.createRef<ListTags>()
  }

  componentDidMount() {
    this.pixArt = this.pixArtRef.current
    this.gridBoard = this.gridBoardRef.current
    this.squares = this.gridBoard.listSquare.map(el => el.map(e => e.current))

    this.tagsColumn = this.listTagColumnRef.current.tags.map(e => e.current)
    this.tagsRow = this.tagsRowRef.current.tags.map(e => e.current)
  }
  
  render(): JSX.Element {
    const { initBoard, solvedBoard, size, coloredBoard} = this.props
    const {row, column} = this.getTagValue(solvedBoard)
    return (
      <div
        className = "board animate-fade-in"
        onAnimationEnd = {((e:AnimationEvent) => {this.onAnimationEnd(e); e.stopPropagation()}).bind(this)}
        ref = {this.ref}
      >
        <PixArt 
          size = {size}
          ref = {this.pixArtRef}
        />
        <ListTags
          direction = {VERITCAL}
          listNumber = {column}
          size = {size}
          ref = {this.listTagColumnRef}
        />
        <ListTags
          direction = {HORIZONTAL}
          listNumber = {row}
          size = {size}
          ref = {this.tagsRowRef}
        />
        <GridBoard
          initBoard = {initBoard}
          solvedBoard = {solvedBoard}
          coloredBoard = {coloredBoard}
          board = {this}
          size = {size}
          ref = {this.gridBoardRef}
          gamePage = {this.props.gamePage}
        />
      </div>
    )
  }

  processAt(indexX: number, indexY: number) {
    const square = this.gridBoard.getSquareAtIndex(indexX, indexY)
    if (!square) return 0
    if (square.state.value != 0) return 0
    if (this.isComplete) return 0
    const processValue = square.process()
    this.currentBoard[indexX][indexY] = processValue

    if (processValue === 1) {
      this.countSolved += 1
      this.pixArt.setColor(indexX, indexY, 0x344861)
      if (this.countSolved === this.numberOfActiveValueSquare) {
        this.isComplete = true
        setTimeout( () => this.playAnimationComplete(indexX, indexY), 550)
      }
    }

    const rowValue = this.processRow(indexX)
    if (rowValue) {
      this.tagsRow[indexX].playAnimationComplete(DELAY * (indexY + 1) )
      for (let y = 0 ; y < this.props.size; y ++) {
        this.squares[indexX][y].playAnimationComplete(DELAY * Math.abs((y - indexY)))
      }
    }

    const columnValue = this.processColumn(indexY)
    if (columnValue) {
      this.tagsColumn[indexY].playAnimationComplete(DELAY * (indexX + 1) )
      for (let x = 0 ; x < this.props.size; x ++) {
        this.squares[x][indexY].playAnimationComplete(DELAY * Math.abs((x - indexX)))
      }
    }

    return processValue
  }

  processRow(x: number) {
    const currentBoard = this.currentBoard
    const solvedBoard = this.props.solvedBoard
    const size = this.props.size
    const tag = this.row[x]
    const tagValue = this.row[x].map(() => false)
    let count = 0
    let valueTagIndex = 0

    if (this.tagsRow[x].state.isDone) return false
    for (let y = 0 ; y < size; y ++) {
      if (currentBoard[x][y] === 0) {
        break
      }
      if (currentBoard[x][y] > 0 && solvedBoard[x][y] === currentBoard[x][y]) {
        count += 1
        if (count === tag[valueTagIndex]) {
          tagValue[valueTagIndex] = true
          count = 0
          valueTagIndex += 1
        }
      }
    }

    count = 0
    valueTagIndex = tag.length - 1

    for (let y = size - 1 ; y > 0; y --) {
      if (currentBoard[x][y] === 0) {
        break
      }
      if (currentBoard[x][y] > 0 && solvedBoard[x][y] === currentBoard[x][y]) {
        count += 1
        if (count === tag[valueTagIndex]) {
          tagValue[valueTagIndex] = true
          count = 0
          valueTagIndex -= 1
        }
      }
    }

    this.tagsRow[x].applyProcessValue(tagValue)

    return currentBoard[x].filter(v => v === 1).length === solvedBoard[x].filter(v => v === 1).length
  }

  processColumn(y: number) {
    const currentBoard = this.currentBoard
    const solvedBoard = this.props.solvedBoard
    const size = this.props.size
    const tag = this.column[y]
    const tagValue = this.column[y].map(() => false)

    if (this.tagsColumn[y].state.isDone) return false
    let count = 0
    let valueTagIndex = 0
    for (let x = 0 ; x < size; x ++) {
      if (currentBoard[x][y] === 0) {
        break
      }
      if (currentBoard[x][y] > 0 && solvedBoard[x][y] === currentBoard[x][y]) {
        count += 1
        if (count === tag[valueTagIndex]) {
          tagValue[valueTagIndex] = true
          count = 0
          valueTagIndex += 1
        }
      }
    }

    count = 0
    valueTagIndex = tag.length - 1

    for (let x = size - 1 ; x > 0; x --) {
      if (currentBoard[x][y] === 0) {
        break
      }
      if (currentBoard[x][y] > 0 && solvedBoard[x][y] === currentBoard[x][y]) {
        count += 1
        if (count === tag[valueTagIndex]) {
          tagValue[valueTagIndex] = true
          count = 0
          valueTagIndex -= 1
        }
      }
    }

    this.tagsColumn[y].applyProcessValue(tagValue)

    return currentBoard.map( (value) => value[y]).filter(v => v === 1).length === solvedBoard.map( (value) => value[y]).filter(v => v === 1).length
  }

  private getTagValue (solvedBoard: number[][]) {
    const size = solvedBoard.length
    let countX = 0
    let countY = 0
    const row: number[][] = []
    const column: number[][] = []
    for (let x = 0 ; x < size ; x++) {
      const curX = []
      const curY = []
      countX = 0
      countY = 0
      for (let y = 0 ; y < size; y++) {
        if (solvedBoard[x][y] != -1) {
          countX += 1
        } else {
          if (countX != 0) {
            curX.push(countX)
            countX = 0
          }
          
        }

        if (solvedBoard[y][x] != -1) {
          countY += 1
        } else {
          if (countY != 0) {
            curY.push(countY)
            countY = 0
          }
        }
      }
      if (countX != 0)
        curX.push(countX)
      
      if (countY != 0)
        curY.push(countY)

      row.push(curX)
      column.push(curY)
    }
    return {row, column}
  }

  playAnimationComplete(indexX: number, indexY: number) {
    const {size} = this.props

    this.pixArt.playAnimationBoardComplete(distance(0, 0, indexX, indexY) * DELAY * 50 / size)

    for (let i = 0 ; i < size ; i ++) {
      this.tagsRow[i].playAnimationBoardComplete(distance(i, 0, indexX, indexY) * DELAY * 50 / size)
      this.tagsColumn[i].playAnimationBoardComplete(distance(0, i, indexX, indexY) * DELAY * 50 / size)
    }

    this.gridBoard.playAnimationComplete()

    for (let x = 0 ; x < size; x++) {
      for (let y = 0; y < size; y++) {
        const square = this.gridBoard.getSquareAtIndex(x, y)
        square.playAnimationBoardComplete(distance(x, y, indexX, indexY) * DELAY * 50 / size)
      }
    }

    const durationTime = Math.max(distance(0, 0, indexX, indexY), distance(size, size, indexX, indexY)) * DELAY * 50 / size + 550
    this.gridBoard.playAnimationBoardComplete(durationTime)
    this.props.gamePage.playAnimationComplete(durationTime)
  }
}