import React from "react"
import { VERITCAL } from "../../constants/gameConstants"
import removeClassStartWith from "../../utils/removeClassStartWith"

export interface TagProps {
  direction: string,
  listNumber: number[]
}

export interface TagState {
  listIsSolved: boolean[]
  isDone: boolean
}

export default class Tag extends React.Component<TagProps, TagState> {
  ref: React.RefObject<HTMLDivElement>
  numberRef: React.RefObject<HTMLSpanElement>[]
  onAnimationEnd: Function
  constructor(props: TagProps) {
    super(props)
    this.state = {
      listIsSolved: this.props.listNumber.map(() => false),
      isDone: false
    }
    this.ref = React.createRef<HTMLDivElement>()
    this.numberRef = this.props.listNumber.map(() => React.createRef<HTMLSpanElement>())
  }

  shouldComponentUpdate(nextProps: Readonly<TagProps>, nextState: Readonly<TagState>) {
    for (let i = 0 ; i < nextState.listIsSolved.length; i ++) {
      if (nextState.listIsSolved[i] != this.state.listIsSolved[i]) return true
    }
    
    return nextState.isDone != this.state.isDone
  }

  render (): JSX.Element {
    const direction = this.props.direction
    const listNumber = this.props.listNumber
    const listIsSolved = this.state.listIsSolved
    let className = 'tag-'
    if (direction === VERITCAL) {
      className += 'v'
    } else {
      className += 'h'
    }

    this.state.isDone
    if (this.state.isDone) className += ' animate-tag-complete'

    return (
      <div
        ref = {this.ref}
        className = {className}
        onAnimationEnd = {((e: AnimationEvent) => {this.onAnimationEnd(e)}).bind(this)}
      >
        {
          listNumber.map(
            (value, i) => (
              <span
                className = {`number-${listIsSolved[i] ? '1' : '0'} number-size-${listNumber.length} ${this.state.listIsSolved[i] ? 'animate-number-disable': ''}`}
                key = {i}
                ref = {this.numberRef[i]}
                onAnimationEnd = {((e: AnimationEvent) => {e.stopPropagation()}).bind(this)}
              >
                {value}
              </span>
            )
          )
        }
      </div>
    )
  }

  playAnimationComplete(delay = 0, duration = 500) {
    setTimeout( ( () => {
      this.ref.current.style.setProperty('--animation-duration', `${duration}ms`)
    
      this.setState({
        listIsSolved: this.state.listIsSolved.map(() => true),
        isDone: true
      })
      this.onAnimationEnd = (e: AnimationEvent) => {
        this.ref.current.className = removeClassStartWith(this.ref.current.className, 'animate') + ' tag-disable'
        e.stopPropagation()
      }
    }).bind(this), delay)
    
  }

  playAnimationBoardComplete(delay = 0, duration = 500) {
    setTimeout( (() => {
      this.ref.current.style.setProperty('--animation-duration', `${duration}ms`)

      this.ref.current.className += ' animate-fade-out'
  
      this.onAnimationEnd = (e: AnimationEvent) => {
        
        e.stopPropagation()
      }
    }).bind(this),
    delay)
  }

  applyProcessValue(values: boolean[]) {
    this.setState({listIsSolved: values})
  }
}