import React from "react"
import { hot } from "react-hot-loader"
import "./../assets/scss/App.scss"
import GamePage from "./Page/GamePage/GamePage"
class App extends React.Component<any, any> {
  fontSize: number 
  width: number
  realFontSize: number 
  constructor(props: any) {
    super(props)
    const computedStyle = window.getComputedStyle(document.documentElement)
    this.realFontSize = parseFloat(computedStyle.fontSize)
    this.width = parseFloat(computedStyle.width)
    this.fontSize = this.width * 0.01
    // console.log(fontSize, width, realFontSize)
    const scale = this.fontSize/this.realFontSize
    document.documentElement.style.fontSize = `${1*scale}vw`

  }

  public render() {
    return (
      <div className="app">
        <GamePage/>
      </div>
    )
  }
}

declare let module: Record<string, unknown>

export default hot(module)(App)
