move-left {\n  100% {\n    transform: translate3d(calc(#{$pixelArtSize} * -1 / 2), calc(#{$heightTagVertical}*-0.5), 0px)\n  }\n}\n\n@for $i from 0 through 5 {\n  $size: $i * 5;\n  $squareSize: 100 / $size;\n  $squareSizex5: 100 / $i;\n  $squareSizex5Percent: $squareSizex5 + \"%\";\n  .gridboard-#{$size} {\n    display: grid;\n    grid-template-columns: repeat($size, unquote($squareSize + \"%\"));\n    grid-template-rows: repeat($size, unquote($squareSize + \"%\"));\n    position: relative;\n    width: 100%;\n    height: 100%;\n    // box-sizing: border-box;\n    // border: 5vw solid #000;\n    overflow: hidden;\n    transform: translate3d(0px, 0px, 0px);\n  }\n\n  $hardlineSize: 0.6vw; //3 pixel\n  .gridboard-line-#{$size}::before,\n  .gridboard-line-#{$size}::after {\n    // content: '';\n    // position: absolute;\n    // top: 0;\n    // left: 0;\n    // width: calc(100%);\n    // height: calc(100%);\n    // background: linear-gradient(to right, #000 $hardlineSize, transparent $hardlineSize);\n    // background-size: calc(#{$squareSizex5Percent} - #{$hardlineSize} / #{$i});\n    // pointer-events: none;\n    // z-index: 100;\n  }\n\n  .gridboard-line-#{$size}::after {\n    transform: rotate(90deg);\n  }\n\n  .list-tag-v-#{$size} {\n    display: grid;\n    grid-template-columns: repeat($size, unquote($squareSize + \"%\"));\n    width: 100%;\n  }\n\n  .list-tag-h-#{$size} {\n    display: grid;\n    grid-template-rows: repeat($size, unquote($squareSize + \"%\"));\n    height: 100%;\n  }\n\n  .pixart-#{$size} {\n    display: grid;\n    grid-template-columns: repeat($size, unquote($squareSize + \"%\"));\n    grid-template-rows: repeat($size, unquote($squareSize + \"%\"));\n    position: relative;\n    width: $pixelArtSize;\n    height: $pixelArtSize;\n    overflow: hidden;\n  }\n\n  .gridboard-hardline-#{$size} {\n    display: grid;\n    grid-template-columns: repeat($i, unquote($squareSizex5 + \"%\"));\n    grid-template-rows: repeat($i, unquote($squareSizex5 + \"%\"