import React from "react"
import Square from "./Square"
import PointerController from "./PointerController"
import Board, { BoardProps } from './Board'
import removeClassStartWith from "../../utils/removeClassStartWith"

export interface GridBoardProps extends BoardProps {
  board: Board
}

export interface GridBoardState {
  isComplete: boolean
}

export default class GridBoard extends React.PureComponent<GridBoardProps, GridBoardState> {
  mainRef: React.RefObject<HTMLDivElement>
  ref: React.RefObject<HTMLDivElement>
  nameRef: React.RefObject<HTMLDivElement>
  hardlineRef: React.RefObject<HTMLDivElement>
  listSquare: React.RefObject<Square>[][]
  pointerController: PointerController
  onAnimationEnd: Function
  constructor(props: GridBoardProps) {
    super(props)
    this.state = {
      isComplete: false
    }
    this.mainRef = React.createRef()
    this.ref = React.createRef()
    this.nameRef = React.createRef()
    this.hardlineRef = React.createRef()
    this.listSquare = []
    for (let i = 0 ; i < this.props.size; i++) {
      const lst = []
      for (let j = 0; j < this.props.size; j++) {
        const ref = React.createRef<Square>()
        lst.push(ref)
      }
      this.listSquare.push(lst)
    }
    this.pointerController = new PointerController(this)
  }

  render (): JSX.Element {
    console.log('board render')
    const size = this.props.size
    const className = `gridboard-${size} gridboard-line-${size}`
    return (
      <div 
        className = "gridboard-parent"
        onAnimationEnd = {((e:AnimationEvent) => {this.onAnimationEnd(e); e.stopPropagation()}).bind(this)}
        onPointerMove = {this.handlePointerMove.bind(this)}
        onPointerDown = {this.handlePointerDown.bind(this)}
        ref = {this.mainRef}
      >
        <div
          className = {`gridboard-hardline-${size}`}
          ref = {this.hardlineRef}
        >
          {Array(size/5)
            .fill(1)
            .map((el, x) => Array(size/5)
              .fill(1)
              .map(
                (el, y) => {
                  let className = "gridboard-square-hardline"
                  if (x === 0) {
                    className += " gridboard-square-hardline-top"
                  }
                  if (x === size/5 - 1) {
                    className += " gridboard-square-hardline-bottom"
                  }
                  if (y === 0) {
                    className += " gridboard-square-hardline-left"
                  }
                  if (y === size/5 - 1) {
                    className += " gridboard-square-hardline-right"
                  }
                  return (
                    <div className = {className} key = {`${x}-${y}`}> </div>
                  )
                }
              )
            )
          }
        </div>
        <div 
          className = {className}
          ref = {this.ref}
        >
          {Array(size)
            .fill(1)
            .map((el, x) => Array(size)
              .fill(1)
              .map(
                (el, y) => (
                  <Square ref = {this.listSquare[x][y]}
                    initValue = {this.props.initBoard[x][y]}
                    solvedValue = {this.props.solvedBoard[x][y]}
                    color = {this.props.coloredBoard[x][y]}
                    indexX = {x}
                    indexY = {y}
                    pointerController = {this.pointerController}
                    key={`square-${x}-${y}`}
                  />
                )
              )
            )
          }    
        </div>
        <div 
          className = "name-board"
          hidden = {true}
          ref = {this.nameRef}
        >
          Croissant
        </div>    
      </div>
    )
  }

  processAt(indexX: number, indexY: number) {
    return this.props.board.processAt(indexX, indexY)
  }

  handlePointerDown(e: PointerEvent) {
    const {x, y} = this.getPositionPointerRelateToElement(e)
    this.pointerController.handlePointerDown({x, y}, e)
  }

  handlePointerMove(e: PointerEvent) {
    const {x, y} = this.getPositionPointerRelateToElement(e)
    this.pointerController.handlePointerMove({x, y}, e)
  }

  getPositionPointerRelateToElement(e: PointerEvent) {
    const x = e.pageX - this.mainRef.current.offsetLeft
    const y = e.pageY - this.mainRef.current.offsetTop

    return {x, y}
  }

  getSquareIndexAtPosition(x: number, y: number) {
    const width = this.ref.current.offsetWidth
    const height = this.ref.current.offsetHeight
    const indexY = Math.floor(x / (width / this.props.size))
    const indexX = Math.floor(y / (height / this.props.size))
    return {x: indexX, y: indexY}
  }

  getSquareAtPosition(x: number, y: number) {
    const {x: indexX, y: indexY} = this.getSquareIndexAtPosition(x, y)
    return this.getSquareAtIndex(indexX, indexY)
  }

  getSquareAtIndex(x: number, y: number) {
    if (x >= 0 && y >= 0 &&
        x < this.props.size && y < this.props.size
    ) {
      return this.listSquare[x][y].current
    }
    
    return null
  }

  playAnimationComplete(delay = 0, duration = 500) {
    setTimeout( () => {
      this.hardlineRef.current.style.setProperty('--animation-duration', `${duration}ms`)
      this.hardlineRef.current.className += " animate-fade-out"
      
      this.onAnimationEnd = ((e: AnimationEvent) => {
        // this.hardlineRef.current.className = removeClassStartWith(this.ref.current.className, "animate")
        e.stopPropagation()
      }).bind(this)
    }, delay)
    
  }

  playAnimationBoardComplete(delay = 0, duration = 500) {
    this.addBorderBoard(delay, duration)
    this.moveLeft(delay, duration)
    this.showBoardName(delay, duration)
  }

  addBorderBoard(delay = 0, duration = 500) {
    setTimeout( () => {
      this.ref.current.style.setProperty('--animation-duration', `${duration}ms`)
      this.ref.current.className += " gridboard-border-end"
      this.onAnimationEnd = () => {}
    }, delay)
  }

  moveLeft(delay = 0, duration = 500) {
    setTimeout( () => {
      this.mainRef.current.style.setProperty('--animation-duration', `${duration}ms`)
      this.mainRef.current.className += ' animate-board-move-left'
      this.onAnimationEnd = () => {}
    }, delay)
  }

  showBoardName(delay = 0, duration = 500) {
    setTimeout( () => {
      this.nameRef.current.style.setProperty('--animation-duration', `${duration}ms`)
      this.nameRef.current.className += ' animate-fade-in'
      this.nameRef.current.hidden = false
      this.onAnimationEnd = () => {}
    }, delay)
  }
}